* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.btn-box {
  justify-content: center;
  padding-top: 200px;
  display: flex;
}

.btn-switcher {
  letter-spacing: 2px;
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 10px;
  margin: 10px 20px;
  padding: 15px 30px;
  font-size: 24px;
  font-weight: bold;
  transition: box-shadow .15s ease-in-out;
  display: inline-block;
  box-shadow: inset 0 0 40px 40px #99f, 0 0 #99f;
}

.btn-switcher:hover {
  color: #30f;
  box-shadow: inset 0 0 10px #30f, 0 0 10px 4px #30f;
}

.btn-switcher:disabled {
  color: #fff;
  cursor: not-allowed;
  box-shadow: inset 0 0 40px 40px #a5a4a5, 0 0 #a5a4a5;
}

